import './App.css';


import { useState } from 'react';
import './App.css';
let deckdata;
let deckID;
let currentCard;
let drawnCardImage;

let cards = [];



//setting up deck
async function DeckSetup() {
  console.log("deck is setting up");
  //get deck file
  const response = await fetch("https://www.deckofcardsapi.com/api/deck/new/shuffle/?deck_count=1")
  console.log("deck file is " + response);
  //grab data from deck file
  deckdata = await response.json();
  //get deck id
  deckID = deckdata.deck_id;
  console.log("dek id is " + deckID)
  await drawCard()
  
}

async function drawCard () {
  console.log("drawn card")
  //making link that draws new card
  let drawCardLink = "https://www.deckofcardsapi.com/api/deck/" + deckID + "/draw/?count=1";
  const drawnCard = await fetch(drawCardLink)
  //grab card data from card file
  currentCard = await drawnCard.json();
  console.log(currentCard.cards[0].image);
  drawnCardImage = currentCard.cards[0].image;
  return(drawnCardImage);
}
await DeckSetup();


console.log(deckdata);
const App = () =>{

   

	return (
    <div className="App">
      <CounterComponent />
    </div>
    
	)
}



const CounterComponent = () =>{
  const [card_list, setCard_list] = useState([]);
  const [displayingCardList, setDisplayingCardList] = useState("");
  
  async function buttonClickEvent() {
    console.log("button click even is called")
    //this returns link for card src
    let newCardsrc = await drawCard();


    cards.push(newCardsrc);
    setCard_list(cards)
    console.log(card_list)
    setDisplayingCardList(newCardsrc);
  }

  function addCard() {
    cards = [];
    setCard_list([]);

    
  }

  return(
      <div>
        <div className="cardBoard">
          <div className='cardField'>
            {card_list.map((card) => (
              <Card cardSrc={card}/>
            ))}
            
          </div>
          

          <div className="Buttons">
            <button className="button" onClick={buttonClickEvent}>+</button>
            <button className="button" onClick={addCard}>+</button>
          </div>
        </div>
      </div>
  )
  
}

const Card = (props) => {
  return(
    <div>
      <img src={props.cardSrc} alt='no card found'></img>

    </div>
  )
}


export default App;